import { createRouter, createWebHashHistory } from 'vue-router'
import Home from './views/Home.vue'
// import Consejo from './views/Consejo.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    props: true
  }
  // {
  //   path: '/consejo',
  //   name: 'Consejo',
  //   component: Consejo,
  //   props: true
  // }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
