    height: auto;
    &.frame_2 {
      width: 20vw;
      margin-top: 10%; 
      margin-bottom: 5%; 
    }
  }
  .adios {
    position: absolute;
    bottom: 10vh;
    left: 15%;
    width: 70%;
    .mano {
      -webkit-appearance: none!important;
      width: 100%;
      // height: 2px;
      outline: none;
      padding: 0;
      margin: 0;
      transition: all .2s ease-out;
      // Range Handle
      &::-webkit-slider-thumb {
        -webkit-appearance: none;
        width: 7rem;
        height: 7rem;
        background-image: url('../../public/img/mano.svg');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center top;
        cursor: pointer;
        margin-top: -1em;
        border: none;
        transition: all .2s ease;
        &:hover {margin-top: -2em;}
      }
      &::-webkit-slider-runnable-track {
        width: 100%;
        height: 5rem;
        cursor: pointer;
        background-image: url('../../public/img/mano_slide.svg');
        background-size: 100% 100%;
        background-position: center;
      }
    }
    p {
      text-align: center;
      width: 100%;
      margin-top: 2em;
    }
  }
}
